<template>
  <view class="chat-page">
    <!-- ‰æßËæπÂºπÂá∫Â±ÇÔºà‰ªéÂ∑¶ÂêëÂè≥ÊªëÂá∫Ôºâ -->
    <view 
      class="sidebar-mask" 
      v-if="showSidebar"
      @click="toggleSidebar"
    ></view>
    <view 
      class="sidebar" 
      v-if="showSidebar"
      :class="{ slideIn: showSidebar }"
    >
      <view class="sidebar-header">
        <text class="sidebar-title">ÂäüËÉΩËèúÂçï</text>
        <button class="close-btn" @click="toggleSidebar">‚úï</button>
      </view>
      <view class="sidebar-menu">
        <view class="menu-item" @click="clearHistory">
          <text class="menu-icon">üóëÔ∏è</text>
          <text class="menu-text">Ê∏ÖÁ©∫ËÅäÂ§©ËÆ∞ÂΩï</text>
        </view>
        <view class="menu-item" @click="switchTheme">
          <text class="menu-icon">üé®</text>
          <text class="menu-text">ÂàáÊç¢‰∏ªÈ¢ò</text>
        </view>
        <view class="menu-item" @click="showSettings">
          <text class="menu-icon">‚öôÔ∏è</text>
          <text class="menu-text">ËÆæÁΩÆ</text>
        </view>
        <view class="menu-item" @click="showAbout">
          <text class="menu-icon">‚ÑπÔ∏è</text>
          <text class="menu-text">ÂÖ≥‰∫é</text>
        </view>
      </view>
    </view>
    
    <!-- È°∂ÈÉ®ÂØºËà™Ê†èÔºàÂ∑¶‰æß‰∏∫‰∏âÈÅìÊù†ÊåâÈíÆÔºâ -->
    <view class="chat-header">
      <button class="menu-trigger" @click="toggleSidebar">
        <text class="hamburger">‚ò∞</text>
      </button>
      <view class="header-content">
        <text class="header-title">ËÅäÂ§©</text>
      </view>
      <view class="header-placeholder"></view> <!-- Âç†‰ΩçÂÖÉÁ¥†Ôºå‰øùÊåÅÊ†áÈ¢òÂ±Ö‰∏≠ -->
    </view>
    
    <!-- ËÅäÂ§©ÂÜÖÂÆπÂå∫Âüü -->
    <scroll-view 
      class="chat-container" 
      scroll-y 
      :scroll-top="scrollTop"
      :style="{ transform: showSidebar ? 'translateX(70%)' : 'translateX(0)' }"
      @scroll="onScroll"
    >
      <!-- Êó∂Èó¥ÂàÜÂâ≤Á∫ø -->
      <view class="time-divider" v-if="showTimeDivider">
        <text>{{ formatTime(new Date()) }}</text>
      </view>
      
      <!-- Ê¨¢ËøéÊ∂àÊÅØ -->
      <view class="message-item ai-message" v-if="messages.length === 0">
        <view class="avatar">
          <image src="/static/ai-avatar.png" mode="widthFix"></image>
        </view>
        <view class="message-bubble">
          <text>‰Ω†Â•ΩÔºÅÊàëÊòØ‰Ω†ÁöÑÂä©ÊâãÔºåÊúâ‰ªÄ‰πàÂèØ‰ª•Â∏ÆÂä©‰Ω†ÁöÑÂêóÔºü</text>
        </view>
      </view>
      
      <!-- Ê∂àÊÅØÂàóË°® -->
      <view 
        v-for="(msg, index) in messages" 
        :key="index" 
        :class="['message-item', msg.isUser ? 'user-message' : 'ai-message']"
      >
        <view class="avatar">
          <image 
            :src="msg.isUser ? '/static/user-avatar.png' : '/static/ai-avatar.png'" 
            mode="widthFix"
          ></image>
        </view>
        <view 
          class="message-bubble" 
          :class="{ voice: msg.isVoice }"
          @click="msg.isVoice && playVoice(msg, index)"
        >
          <template v-if="msg.isVoice">
            <view class="voice-content">
              <view class="voice-wave" :class="{ playing: msg.isPlaying }">
                <view class="wave-bar"></view>
                <view class="wave-bar"></view>
                <view class="wave-bar"></view>
              </view>
              <text class="voice-duration">{{ msg.duration }}"</text>
            </view>
          </template>
          <template v-else>
            <text>{{ msg.content }}</text>
          </template>
        </view>
      </view>
      
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view class="loading-indicator" v-if="isLoading">
        <view class="loading-dot"></view>
        <view class="loading-dot"></view>
        <view class="loading-dot"></view>
      </view>
    </scroll-view>
    
    <!-- ËæìÂÖ•Âå∫Âüü -->
    <view class="input-container" :style="{ transform: showSidebar ? 'translateX(70%)' : 'translateX(0)' }">
      <!-- ‰∏ªËæìÂÖ•Ê†è -->
      <view class="input-bar">
        <!-- Â∑¶‰æßÔºöËØ≠Èü≥/ÊâìÂ≠óÂàáÊç¢ÊåâÈíÆ -->
        <button 
          class="switch-btn" 
          @click="toggleMode"
        >
          {{ isTextMode ? 'üé§' : '‚å®Ô∏è' }}
        </button>
        
        <!-- ‰∏≠Èó¥ÔºöËæìÂÖ•Ê°ÜÔºàÈªòËÆ§‰∏ÄË°åÔºâ -->
        <view class="input-wrapper">
          <!-- ÊñáÂ≠óËæìÂÖ•Ê®°Âºè -->
          <textarea 
            v-if="isTextMode"
            class="text-input" 
            placeholder="ËØ∑ËæìÂÖ•Ê∂àÊÅØ..." 
            v-model="inputMessage"
            :style="{ height: textareaHeight + 'px' }"
            @input="adjustTextareaHeight"
            @focus="onInputFocus"
            @blur="onInputBlur"
            @confirm="sendMessage"
          ></textarea>
          
          <!-- ËØ≠Èü≥ËæìÂÖ•Ê®°Âºè -->
          <view 
            v-else 
            class="voice-input"
            @touchstart="startRecording"
            @touchend="handleRecordEnd"
            @touchmove="handleTouchMove"
            @touchcancel="stopRecording(true)"
          >
            <text class="voice-hint">{{ isRecording ? 'ÊùæÂºÄÂèëÈÄÅ' : 'Êåâ‰ΩèËØ¥ËØù' }}</text>
          </view>
        </view>
        
        <!-- Âè≥‰æßÔºöÂä†Âè∑ÊåâÈíÆ -->
        <button 
          class="plus-btn" 
          @click.stop="toggleFunctionButtons"
        >
          +
        </button>
      </view>
      
      <!-- Â∫ïÈÉ®ÂäüËÉΩÊåâÈíÆ -->
      <view class="function-buttons" v-if="showFunctionButtons">
        <view class="function-item" @click="chooseImage">
          <view class="func-icon">üì∑</view>
          <text class="func-text">Áõ∏ÂÜå</text>
        </view>
        <view class="function-item" @click="takePhoto">
          <view class="func-icon">üì∏</view>
          <text class="func-text">ÊãçÊëÑ</text>
        </view>
        <view class="function-item" @click="sendLocation">
          <view class="func-icon">üìç</view>
          <text class="func-text">‰ΩçÁΩÆ</text>
        </view>
        <view class="function-item" @click="showMore">
          <view class="func-icon">‚ãÆ</view>
          <text class="func-text">Êõ¥Â§ö</text>
        </view>
      </view>
    </view>
    
    <!-- ËØ≠Èü≥ÂΩïÂà∂ÊèêÁ§∫ -->
    <view class="voice-recording-toast" v-if="isRecording && !showSidebar">
      <view class="recording-icon">{{ isCancel ? '‚úï' : 'üé§' }}</view>
      <text class="recording-text">{{ isCancel ? 'ÊùæÂºÄÂèñÊ∂à' : 'Ê≠£Âú®ÂΩïÈü≥...' }}</text>
      <text class="recording-time">{{ recordingTime }}"</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      messages: [],
      inputMessage: '',
      isTextMode: true,
      isRecording: false,
      isLoading: false,
      scrollTop: 0,
      showTimeDivider: true,
      textareaHeight: 36, // ÈªòËÆ§‰∏ÄË°åÈ´òÂ∫¶
      isInputFocused: false,
      recordingTime: 0,
      recordingTimer: null,
      recorderManager: null,
      innerAudioContext: null,
      isCancel: false,
      startY: 0,
      showFunctionButtons: false,
      currentPlayingIndex: -1,
      showSidebar: false, // ‰æßËæπÊ†èÊòæÁ§∫Áä∂ÊÄÅ
      darkMode: false // ‰∏ªÈ¢òÂàáÊç¢
    };
  },
  onLoad() {
    // ÂàùÂßãÂåñÂΩïÈü≥ÂíåÈü≥È¢ëÊí≠Êîæ
    this.recorderManager = uni.getRecorderManager();
    this.innerAudioContext = uni.createInnerAudioContext();
    this.innerAudioContext.onEnded(() => this.stopVoicePlay());
    
    // ÁõëÂê¨ÂΩïÈü≥ÂÆåÊàê‰∫ã‰ª∂
    this.recorderManager.onStop((res) => {
      if (!this.isCancel && this.recordingTime >= 1) {
        this.messages.push({
          content: '[ËØ≠Èü≥Ê∂àÊÅØ]',
          isUser: true,
          isVoice: true,
          duration: this.recordingTime,
          filePath: res.tempFilePath,
          isPlaying: false,
          timestamp: Date.now()
        });
        this.saveHistory();
        this.scrollToBottom();
        this.getAIResponse('ËØ≠Èü≥Ê∂àÊÅØ');
      }
    });
    
    // Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩï
    this.loadHistory();
  },
  onUnload() {
    this.stopVoicePlay();
    this.innerAudioContext.destroy();
    this.saveHistory();
  },
  methods: {
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(date) {
      const hours = date.getHours().toString().padStart(2, '0');
      const minutes = date.getMinutes().toString().padStart(2, '0');
      return `${hours}:${minutes}`;
    },
    
    // Âä†ËΩΩ/‰øùÂ≠òÂéÜÂè≤ËÆ∞ÂΩï
    loadHistory() {
      const history = uni.getStorageSync('aiChatHistory');
      if (history && history.length) {
        this.messages = history;
        this.scrollToBottom();
        this.showTimeDivider = false;
      }
    },
    saveHistory() {
      if (this.messages.length) {
        uni.setStorageSync('aiChatHistory', this.messages);
      }
    },
    
    // ÂàáÊç¢ËØ≠Èü≥/ÊñáÂ≠óËæìÂÖ•Ê®°Âºè
    toggleMode() {
      this.isTextMode = !this.isTextMode;
      this.inputMessage = '';
      this.showFunctionButtons = false;
    },
    
    // ÂèëÈÄÅÊñáÂ≠óÊ∂àÊÅØ
    sendMessage() {
      if (!this.isTextMode || !this.inputMessage.trim()) return;
      
      const userMsg = {
        content: this.inputMessage.trim(),
        isUser: true,
        isVoice: false,
        timestamp: Date.now()
      };
      
      this.messages.push(userMsg);
      this.inputMessage = '';
      this.textareaHeight = 36;
      this.saveHistory();
      this.scrollToBottom();
      this.getAIResponse(userMsg.content);
    },
    
    // AIÂõûÂ§çÊ®°Êãü
    getAIResponse(input) {
      this.isLoading = true;
      setTimeout(() => {
        let response = input.includes('ËØ≠Èü≥') 
          ? 'ÊàëÊî∂Âà∞‰∫Ü‰Ω†ÁöÑËØ≠Èü≥Ê∂àÊÅØÔºåËøôÊòØÂõûÂ§ç~' 
          : `‰Ω†ËØ¥Ôºö"${input}"ÔºåËøôÊòØÊàëÁöÑÂõûÂ§ç`;
        
        this.messages.push({
          content: response,
          isUser: false,
          isVoice: false,
          timestamp: Date.now()
        });
        this.isLoading = false;
        this.saveHistory();
        this.scrollToBottom();
      }, 1500);
    },
    
    // ÂΩïÈü≥ÂäüËÉΩ
    startRecording(e) {
      if (this.isTextMode) return;
      
      this.startY = e.touches[0].clientY;
      this.isCancel = false;
      this.isRecording = true;
      this.recordingTime = 0;
      
      this.recorderManager.start({
        duration: 60000,
        format: 'mp3'
      });
      
      this.recordingTimer = setInterval(() => {
        this.recordingTime++;
        if (this.recordingTime >= 60) this.handleRecordEnd();
      }, 1000);
    },
    
    handleRecordEnd(e) {
      if (!this.isRecording) return;
      
      const currentY = e?.changedTouches?.[0]?.clientY || this.startY;
      this.isCancel = this.startY - currentY > 50;
      this.stopRecording(this.isCancel);
    },
    
    stopRecording(cancel) {
      if (!this.isRecording) return;
      
      this.recorderManager.stop();
      this.isRecording = false;
      clearInterval(this.recordingTimer);
      
      if (cancel) {
        uni.showToast({ title: 'Â∑≤ÂèñÊ∂à', icon: 'none' });
      } else if (this.recordingTime < 1) {
        uni.showToast({ title: 'ÂΩïÈü≥Êó∂Èó¥Â§™Áü≠', icon: 'none' });
      }
      
      setTimeout(() => {
        this.recordingTime = 0;
        this.isCancel = false;
      }, 500);
    },
    
    handleTouchMove(e) {
      if (!this.isRecording) return;
      this.isCancel = this.startY - e.touches[0].clientY > 50;
    },
    
    // ËØ≠Èü≥Êí≠Êîæ
    playVoice(msg, index) {
      if (this.currentPlayingIndex === index) {
        this.stopVoicePlay();
        return;
      }
      
      this.stopVoicePlay();
      this.currentPlayingIndex = index;
      this.messages[index].isPlaying = true;
      
      if (msg.filePath) {
        this.innerAudioContext.src = msg.filePath;
        this.innerAudioContext.play();
      } else {
        setTimeout(() => this.stopVoicePlay(), msg.duration * 1000);
      }
    },
    
    stopVoicePlay() {
      if (this.currentPlayingIndex !== -1) {
        this.messages[this.currentPlayingIndex].isPlaying = false;
        this.innerAudioContext.stop();
        this.currentPlayingIndex = -1;
      }
    },
    
    // Ë∞ÉÊï¥ÊñáÊú¨Ê°ÜÈ´òÂ∫¶
    adjustTextareaHeight(e) {
      // ÈôêÂà∂ÊúÄÂ§ßÈ´òÂ∫¶‰∏∫3Ë°å
      const height = e.target.scrollHeight;
      this.textareaHeight = height > 108 ? 108 : height;
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        const query = uni.createSelectorQuery().in(this);
        query.select('.chat-container').boundingClientRect(data => {
          this.scrollTop = data.scrollHeight;
        }).exec();
      });
    },
    
    // ËæìÂÖ•Ê°ÜËÅöÁÑ¶/Â§±ÁÑ¶
    onInputFocus() {
      this.isInputFocused = true;
      this.showFunctionButtons = false;
    },
    onInputBlur() {
      this.isInputFocused = false;
    },
    
    // ÂàáÊç¢Â∫ïÈÉ®ÂäüËÉΩÊåâÈíÆ
    toggleFunctionButtons() {
      if (this.isInputFocused) {
        uni.hideKeyboard();
        this.isInputFocused = false;
      }
      this.showFunctionButtons = !this.showFunctionButtons;
    },
    
    // ÂäüËÉΩÊåâÈíÆÊìç‰Ωú
    chooseImage() {
      this.showFunctionButtons = false;
      uni.chooseImage({ count: 1, sourceType: ['album'], success: (res) => {
        this.messages.push({
          content: '[ÂõæÁâá]',
          isUser: true,
          isImage: true,
          imageUrl: res.tempFilePaths[0],
          timestamp: Date.now()
        });
        this.scrollToBottom();
      }});
    },
    takePhoto() {
      this.showFunctionButtons = false;
      uni.chooseImage({ count: 1, sourceType: ['camera'], success: (res) => {
        this.messages.push({
          content: '[ÊãçÊëÑÂõæÁâá]',
          isUser: true,
          isImage: true,
          imageUrl: res.tempFilePaths[0],
          timestamp: Date.now()
        });
        this.scrollToBottom();
      }});
    },
    sendLocation() {
      this.showFunctionButtons = false;
      uni.chooseLocation({ success: (res) => {
        this.messages.push({
          content: `[‰ΩçÁΩÆÔºö${res.name}]`,
          isUser: true,
          isLocation: true,
          timestamp: Date.now()
        });
        this.scrollToBottom();
      }});
    },
    showMore() {
      this.showFunctionButtons = false;
      uni.showActionSheet({
        itemList: ['Êñá‰ª∂', 'Êî∂Ëóè', 'ËΩ¨Ë¥¶', 'ÂêçÁâá'],
        success: (res) => {
          const items = ['Êñá‰ª∂', 'Êî∂Ëóè', 'ËΩ¨Ë¥¶', 'ÂêçÁâá'];
          uni.showToast({ title: `ÈÄâÊã©‰∫Ü${items[res.tapIndex]}`, icon: 'none' });
        }
      });
    },
    
    // ‰æßËæπÊ†èÊéßÂà∂ÔºàÊ†∏ÂøÉÂäüËÉΩÔºö‰ªéÂ∑¶ÂêëÂè≥ÂºπÂá∫Ôºâ
    toggleSidebar() {
      this.showSidebar = !this.showSidebar;
      this.showFunctionButtons = false; // ‰æßËæπÊ†èÊòæÁ§∫Êó∂ÈöêËóèÂäüËÉΩÊåâÈíÆ
      
      // Á¶ÅÊ≠¢ËÉåÊôØÊªöÂä®
      const container = uni.createSelectorQuery().in(this).select('.chat-container');
      container.fields({ scrollEnabled: true }, data => {
        container.scrollEnabled(!this.showSidebar).exec();
      }).exec();
    },
    
    // ‰æßËæπÊ†èËèúÂçïÂäüËÉΩ
    clearHistory() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöÊ∏ÖÁ©∫ËÅäÂ§©ËÆ∞ÂΩïÔºü',
        success: (res) => {
          if (res.confirm) {
            this.messages = [];
            this.scrollToBottom();
            uni.removeStorageSync('aiChatHistory');
            this.toggleSidebar();
          }
        }
      });
    },
    switchTheme() {
      this.darkMode = !this.darkMode;
      uni.showToast({ title: this.darkMode ? 'Â∑≤ÂàáÊç¢Ê∑±Ëâ≤Ê®°Âºè' : 'Â∑≤ÂàáÊç¢ÊµÖËâ≤Ê®°Âºè', icon: 'none' });
    },
    showSettings() {
      uni.showToast({ title: 'ËÆæÁΩÆÂäüËÉΩ', icon: 'none' });
      this.toggleSidebar();
    },
    showAbout() {
      uni.showToast({ title: 'ÂÖ≥‰∫éÂä©Êâã v1.0', icon: 'none' });
      this.toggleSidebar();
    },
    
    onScroll() {
      if (this.showFunctionButtons) this.showFunctionButtons = false;
    }
  }
};
</script>

